.about {

	.section-wrap {
		display: flex;
		flex-wrap: wrap; }

	.section-btn,
	.section-img {
		width: calc(25% - 30px);
		margin-right: 30px;
		background-size: cover;
		min-height: 315px;
		@include r(991) {
			width: calc(50% - 15px);
			margin-bottom: 30px; }
		@include r(575) {
			width: 100%;
			margin-right: 0; } }

	.section-btn {
		padding: 60px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		position: relative;
		@include jpg(a1);

		&::before {
			position: absolute;
			content: '';
			top: 30px;
			left: 30px;
			right: 30px;
			bottom: 30px;
			border: 10px solid #fff;
			@include r(1199) {
				top: 20px;
				left: 20px;
				right: 20px;
				bottom: 20px; } }

		* {
			position: relative; } }

	.section-btn-text {
		font-size: 20px;
		line-height: 30px;
		margin-bottom: 20px;
		@include r(1199) {
			font-size: 16px;
			line-height: 24px; } }

	.section-img {
		@include jpg(a2);
		@include r(991) {
			margin-right: 0; }
		@include r(575) {
			display: none; } }

	.section-text {
		width: 50%;
		@include r(991) {
			width: 100%; }

		h2 {
			margin: 0 0 14px;
			font-size: 28px;
			font-weight: 700;
			text-transform: none;
			line-height: 30px; }

		h3 {
			margin: 0 0 10px;
			text-transform: none; }

		p {
			margin: 0 0 10px;
			font-size: 14px;
			line-height: 21px; } } }
