ul.page-numbers li {
  margin: 0 3px;
}

ul.page-numbers a {
  color: #94adba;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
}

ul.page-numbers .prev,
ul.page-numbers .next {
  position: relative;
  color: transparent;
}

ul.page-numbers .prev::before, ul.page-numbers .prev::after,
ul.page-numbers .next::before,
ul.page-numbers .next::after {
  position: absolute;
  content: '';
  width: 11px;
  height: 1px;
  top: 50%;
  left: 50%;
  background-color: #404040;
}

ul.page-numbers .prev::before {
  transform: translate3d(-5px, -4px, 0) rotate(-45deg);
}

ul.page-numbers .prev::after {
  transform: translate3d(-5px, 4px, 0) rotate(45deg);
}

ul.page-numbers .next::before {
  transform: translate3d(-5px, -4px, 0) rotate(45deg);
}

ul.page-numbers .next::after {
  transform: translate3d(-5px, 4px, 0) rotate(-45deg);
}

ul.page-numbers .current {
  color: #143848;
}

