.header {
	min-height: 299px;
	background-repeat: no-repeat;
	background-position: center 9px;
	@include png(header-bg);
	border-bottom: 5px solid $color-accent;

	.header-box {
		display: flex;
		align-items: flex-end; }

	.logo,
	.header-icon {
		flex-shrink: 0; }

	.logo {
		display: block;
		padding-bottom: 25px;
		margin-right: 75px;
		@include r(1259) {
			max-width: 150px;
			margin-right: 25px; }

		&.header-main-logo {
			@include r(991) {
				display: none; } } }

	.header-icon {
		@include r(1259) {
			max-width: 100px; }
		@include r(1199) {
			display: none; } }

	.header-nav {
		min-height: 294px;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
		align-items: flex-end;
		flex-grow: 1;
		@include r(991) {
			align-items: flex-start; }
		@include r(575) {
			flex-direction: column;
			justify-content: flex-start;
			align-items: center; }

		.logo {
			display: none;
			@include r(991) {
				height: 244px;
				padding: 0;
				display: flex;
				align-items: center;
				flex-shrink: 0; }
			@include r(575) {
				height: 120px;
				max-width: 120px;
				margin-bottom: 20px; } } }

	.header-info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-end;
		flex-grow: 1;
		@include r(991) {
			height: 244px;
			flex-shrink: 0; }
		@include r(575) {
			height: auto;
			flex-grow: unset;
			align-items: center;
			margin-bottom: 19px; } }

	.header-title,
	.header-phone {
		color: $color-black;
		font-size: 25px;
		@include r(575) {
			font-size: 19px;
			margin-bottom: 5px; } }

	.header-title {
		font-weight: 500; }

	.header-phone {
		font-weight: 700;
		letter-spacing: .1em;
		text-decoration: none; }

	.header-messengers {
		font-size: 20px;
		font-weight: 500;
		@include r(575) {
			font-size: 17px; }

		a {
			color: $color-black;
			text-decoration: none;
			padding-left: 5px; }

		i {
			font-size: 20px; } }

	.header-menu {
		@include r(991) {
			width: 100%; } }

	.menu-wrap {
		@include r(767) {
			display: none; }

		&.on {
			display: block; } }

	.menu {
		display: flex;
		list-style-type: none;
		padding: 0;
		margin: 0;
		@include r(767) {
			flex-direction: column; }

		li {
			@include r(767) {
				border-bottom: 1px solid $color-light; }

			&:first-child {
				@include r(767) {
					border-top: 1px solid $color-light; } }

			&:not(:first-child) {
				padding-left: 34px;
				position: relative;
				@include r(767) {
					padding-left: 0; }

				&::before {
					position: absolute;
					content: '';
					width: 8px;
					height: 8px;
					top: 50%;
					left: 13px;
					transform: translateY(-50%);
					background-color: #ffb6ca;
					border-radius: 100%;
					@include r(767) {
						display: none; } } }

			a {
				padding: 15px 0;
				color: $color-black;
				font-size: 16px;
				font-weight: 400;
				line-height: 20px;
				text-decoration: none; } } } }

/* ---- TOGGLE ---- */
.menu-toggle {
	display: none;
	width: 35px;
	height: 21px;
	padding: 9px 0;
	position: relative;
	cursor: pointer;
	@include r(767) {
		display: inline-block;
		margin: 14px 0 10px; }

	span {
		position: relative;
		display: block;
		width: 100%;
		height: 3px;
		background-color: $color-black;
		transition: all 0.3s;
		backface-visibility: hidden;
		border-radius: 2px;

		&:after,
		&:before {
			content: '';
			position: absolute;
			left: 0;
			width: 100%;
			height: 3px;
			background-color: $color-black;
			transition: all 0.3s;
			backface-visibility: hidden;
			border-radius: 2px; }

		&:after {
			top: 9px; }

		&:before {
			top: -9px; } }

	&.on {

		span {
			background-color: transparent;

			&:before {
				background-color: $color-black;
				transform: rotate(45deg) translate(5px, 5px); }

			&:after {
				background-color: $color-black;
				transform: rotate(-45deg) translate(7px, -8px); } } } }
